import React from 'react';
import Square from "./Square";

const Board = (props) => {

    const renderSquare = (index) => {
        return(
            <Square value={props.value[index]} onClick={() => props.onClick(index)}/>
        );
    }

    return (
        <>
            <div>
                {renderSquare(0)}
                {renderSquare(1)}
                {renderSquare(2)}
            </div>
            <div>
                {renderSquare(3)}
                {renderSquare(4)}
                {renderSquare(5)}
            </div>
            <div>
                {renderSquare(6)}
                {renderSquare(7)}
                {renderSquare(8)}
            </div>
        </>
    );
};

export default Board;